 4px 4px rgba(33, 33, 33, 0.1);\n  margin: 0;\n  white-space: nowrap;\n  width: 100%;\n  overflow: hidden;\n  text-overflow: ellipsis;\n\n  &:first-letter {\n    text-transform: uppercase;\n  }\n}\n\n.generation {\n  font-size: 24px;\n  display: flex;\n  align-self: flex-end;\n  color: #fdfdfd;\n  white-space: nowrap;\n  margin: 0 6px 3px;\n}\n\n.id {\n  height: 41px;\n  padding: 0 15px;\n  border-radius: 50%;\n  background-color: #f2cb07;\n  font-weight: normal;\n  font-size: 16px;\n  line-height: 41px;\n  text-align: center;\n  color: #000;\n}\n\n.abilities {\n  width: 100%;\n  height: 60px;\n  background: #fdfdfd;\n  box-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);\n  border-radius: 8px;\n  padding: 12px 28px;\n  font-weight: normal;\n  font-size: 16px;\n  line-height: 19px;\n  margin: 0 0 21px;\n\n  & span:first-child {\n    display: block;\n  }\n}\n\n.healthExp {\n  display: flex;\n  width: 100%;\n  height: 60px;\n  background: #fdfdfd;\n  box-shadow: 4px 4px 4px rgba(33, 33, 33, 0.1);\n  border-radius: 8px;\n  padding: 18px 19px 8px;\n  font-weight: normal;\n  font-size: 16px;\n  line-height: 19px;\n  margin-bottom: 21px;\n\n  span {\n    line-height: 0.5;\n  }\n}\n\n.health,\n.exp {\n  width: 50%;\n\n  span:first-child {\n    display: block;\n  }\n}\n\n.health {\n  padding-right: 27px;\n}\n\n.points {\n  font-weight: bold;\n  font-size: 16px;\n  line-height: 19px;\n  color: #000000;\n}\n\n.progressBar {\n  height: 5px;\n  background: #f6f7f9;\n  border-radius: 8px;\n  position: relative;\n}\n\n.progress {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 5px;\n  background: linear-gradient(270deg, #64d368 0.15%, #64d368 70.88%);\n  border-radius: 8px;\n}\n\n.stat {\n  display: flex;\n  flex-wrap: nowrap;\n  justify-content: space-between;\n}\n\n.box {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-around;\n  align-items: center;\n  width: 82px;\n  height: 70px;\n  background: #fdfdfd;\n  box-shadow: 4px 4px 24px rgba(1, 17, 38, 0.2);\n  border-radius: 8px;\n  font-weight: normal;\n  font-size: 12px;\n  line-height: 14px;\n  text-align: center;\n  color: #4b4b4b;\n}\n\n.circle {\n  width: 36px;\n  height: 36px;\n  border-radius: 50%;\n  border: 3px solid $dark;\n  text-align: center;\n  line-height: 32px;\n  font-size: 16px;\n  color: #212121;\n}\n\n.transparent {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  background-color: rgba(33, 33, 33, 0.5);\n  z-index: -1;\n  cursor: pointer;\n}\n\n.label {\n  background: $second;\n  box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.18);\n  border-radius: 11px;\n  height: 1.1vw;\n  min-width: 4.4vw;\n  font-style: normal;\n  font-weight: normal;\n  font-size: 0.9vw;\n  line-height: 1vw;\n  text-align: center;\n  color: $dark;\n  padding: 0 0.9vw 0.1vw;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  margin-right: 0.9vw;\n\n  @include colors();\n}\n\n.button {\n  position: absolute;\n  top: -35px;\n  right: 0;\n  background-color: transparent;\n  border: none;\n  outline: none;\n  font-size: 35px;\n  font-weight: bold;\n  cursor: pointer;\n}\n","$third: #f5db13;\n$primary: #f2b807;\n$second: #f28f16;\n$danger: #d93e30;\n$white: #f2f2f2;\n$dark: #212121;\n\n@mixin colors {\n  &.stile,\n  &.dark,\n  &.rock {\n    background-color: #a1a1a1;\n  }\n\n  &.bug,\n  &.grass {\n    background-color: #70a83b;\n  }\n\n  &.ice,\n  &.water {\n    background-color: #a2cff0;\n  }\n\n  &.fire,\n  &.fighting,\n  &.dragon {\n    background-color: #f76545;\n  }\n\n  &.normal,\n  &.gosth {\n    background-color: #76aadb;\n  }\n\n  &.poison,\n  &.psychic,\n  &.fairy,\n  &.ghost {\n    background-color: #a974bc;\n  }\n\n  &.ground {\n    background-color: #9b897b;\n  }\n\n  &.flying,\n  &.electric {\n    background-color: #f7c545;\n  }\n\n  &:last-child {\n    margin-right: 0;\n  }\n}"],"sourceRoot":""}]);
